<style>
  .order-id {
    cursor: pointer;
    color: #2077FF;
  }

  .order-id:hover {
    color: #409EFF;
  }
  .date-wrap-inline {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }
</style>

<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="goAddGoodsPage" size="mini" icon="el-icon-plus">添加供应商活动</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <region class="region-wrap" @RegionChange="RegionChange" :refresh="false"></region>
          <el-form-item label="活动ID/名称">
            <el-input class="search-input" v-model.trim="searchContent.activitySearchVal" placeholder="活动ID/名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="SKU编码/商品名称">
            <el-input class="search-input" v-model.trim="searchContent.goodsInfo" placeholder="SKU编码/商品名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="预售类型">
            <el-select class="search-input" v-model="searchContent.presaleType" filterable placeholder="请选择预售类型" size="small" @change="doSearchHandel(true)" clearable>
               <el-option
                 v-for="item in presaleTypeOptions"
                 :key="item.value"
                 :label="item.label"
                 :value="item.value">
               </el-option>
             </el-select>
           </el-form-item>
           <el-form-item label="周期类型">
            <el-select class="search-input" v-model="searchContent.cycleType" filterable placeholder="请选择周期类型" size="small" @change="doSearchHandel(true)" clearable>
               <el-option
                 v-for="item in cycleTypeOptions"
                 :key="item.value"
                 :label="item.label"
                 :value="item.value">
               </el-option>
             </el-select>
           </el-form-item>
           <el-form-item label="开始时间">
            <el-date-picker
              class="search-input"
              v-model="searchContent.startDate"
              type="datetime"
              placeholder="请选择开始时间"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              size="small"
              @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="结束时间">
            <el-date-picker
              class="search-input"
              v-model="searchContent.endDate"
              type="datetime"
              placeholder="请选择结束时间"
              format="yyyy-MM-dd HH:mm:ss"
              value-format="yyyy-MM-dd HH:mm:ss"
              size="small"
              @change="doSearchHandel(true)">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="状态">
           <el-select class="search-input" v-model="searchContent.activityStatus" filterable placeholder="请选择状态" clearable size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in statusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="70px" prop="id" label="活动ID"></el-table-column>
        <el-table-column min-width="120px" prop="activityName" label="预售活动名称"></el-table-column>
        <el-table-column min-width="90px" prop="presaleTypeName" label="预售类型"></el-table-column>
        <el-table-column min-width="90px" prop="regionName" label="活动区域"></el-table-column>
        <el-table-column min-width="90px" prop="cycleTypeName" label="周期类型"></el-table-column>
        <el-table-column min-width="210px" label="有效期">
          <template slot-scope="scope">
            <div class="date-wrap-inline" v-if="scope.row.activityStartDate && scope.row.activityEndDate">
              <div>
                <p>{{scope.row.activityStartDate.substr(0, 10)}}</p>
                <p>{{scope.row.activityStartDate.substr(11, 8)}}</p>
              </div>
              <div>&nbsp;-&nbsp;</div>
              <div>
                <p>{{scope.row.activityEndDate.substr(0, 10)}}</p>
                <p>{{scope.row.activityEndDate.substr(11, 8)}}</p>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="130px" label="截单时间">
          <template slot-scope="scope">
            <div>
              {{scope.row.cutoffTypeName}} {{scope.row.cutoffTime}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="110px" prop="pickUpDate" label="提货日期"></el-table-column>
        <el-table-column min-width="100px" label="预售商品数">
          <template slot-scope="scope" size="small">
            <p class="order-id">
              <router-link :to="scope.row.redirectUrl" target="_blank">{{scope.row.goodsNum}}</router-link>
            </p>
          </template>
        </el-table-column>
        <el-table-column min-width="120px" prop="activityStatus" label="状态" :formatter="activityStatusFormatter"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="140">
          <template slot-scope="scope" size="small">
            <!-- // 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效" -->
            <div>
              <div>
                <el-button v-if="scope.row.activityStatus === 0" @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
                <el-button v-else @click="showItem(scope.row)" size="mini" type="text" icon="el-icon-view">查看</el-button>
                <!-- <el-button @click="copyItem(scope.row)" type="text" size="mini" icon="el-icon-document-copy">复制</el-button> -->
              </div>
              <div>
                <el-button v-if="scope.row.activityStatus === 0 || scope.row.activityStatus === 2 || scope.row.activityStatus === 3" @click="editItem(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
                <el-button  v-if="scope.row.activityStatus === 0" @click="publishActivityHandel(scope.row)" type="text" size="mini" icon="el-icon-upload2">发布</el-button>
                <el-button v-if="scope.row.activityStatus === 3 || scope.row.activityStatus === 2" @click="activeOver(scope.row)" size="mini" icon="el-icon-warning" type="text">终止</el-button>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Region from '@/components/Common/StoreCommon/Region'
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import config from "@/utils/config"
  import requestUrl from '@/api/Supplier'
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        cityOptions: [], // 城市选择列表
        // 搜素条件
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          activitySearchVal: '', // 活动ID、名称
          presaleType: '', // 预售类型，1-单期，2-周期
          cycleType: '', // 周期类型  1 短周期   2  长周期
          startDate: '',
          endDate: '',
          activityStatus: '' // 活动状态 0：未发布，1失效 2未开始 3进行中
        },
        cycleTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '短周期',
            value: 1
          },
          // {
          //   label: '长周期',
          //   value: 2
          // }
        ],
        presaleTypeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '单期',
            value: 1
          },
          {
            label: '周期',
            value: 2
          }
        ],
        // 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"
        statusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '进行中',
            value: 3
          },
          {
            label: '未发布',
            value: 0
          },
          {
            label: '未开始',
            value: 2
          },
          {
            label: '已结束',
            value: 4
          },
          {
            label: '已终止',
            value: 5
          }
        ],
        tableData: []

      }
    },
    mounted() {
      let searchContentMesg = Storage.get("searchContent")
      if(this.$route.meta.keepAlive && searchContentMesg) {
        this.pageNum = searchContentMesg.pageNum
        this.searchContent = searchContentMesg.searchContent
      }
      Storage.remove('searchContent')
      this.doSearchHandel()
    },
    components: {
      "region": Region
    },
    methods: {
      RegionChange(params) {
        this.searchContent.regionCode = params.regionCode
        this.doSearchHandel(true)
      },
      goAddGoodsPage() {
        this.$router.push("/supplier_act/add")
      },
      /**
       * [activityStatusFormatter 格式化状态]
       * @return {[type]} [description]
       */
      activityStatusFormatter(row, column, cellValue, index) {
        if(cellValue === 0) {
          return "未发布"
        }else if(cellValue == 1) {
          return "已发布"
        }else if(cellValue == 2) {
          return "未开始[发布中]"
        }else if(cellValue == 3) {
          return "进行中[发布中]"
        }else if(cellValue == 4) {
          if(row.activityType === 11) {
            return "已结束[发布中]"
          }else {
            return "已结束"
          }
        }else if(cellValue == 5) {
          return "已终止"
        }else if(cellValue == 6) {
          return "已失效"
        }
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandels 搜索]
       * @param  {[type]} _params [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        if(_param === true) {
          this.pageNum = 1
        }
        this.loading = true
        this.$http.post(requestUrl.listPage, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data) {
              if(res.data.result && res.data.result.length) {
                for(let item of res.data.result) {
                  item.redirectUrl = `/supplier_goods?id=${item.id}`
                }
                this.tableData = res.data.result
              }else {
                this.tableData = []
              }
              this.total = res.data.total
            }else {
              this.total = 0
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })

      },
      /**
       * [deleteItem 删除活动]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let activityId = _row.id
        this.$confirm('您确认删除当前未发布的活动吗？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.delActivity, {activityId: activityId, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [publishActivityHandel 发布活动]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
       publishActivityHandel(_row) {
        let activityId = _row.id
        this.$confirm('您确定要执行发布操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.publishActivity, {activityId: activityId, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("发布成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [copyItem 复制活动]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      copyItem(_row) {
        let _paramMesg = {
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)
        this.$router.push(`/supplier_act/edit/${_row.id}/copy`)
      },
      /**
       * [activeOver 失效活动]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      activeOver(_row) {
        let activityId = _row.id
        this.$confirm('您确定要执行试下操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(requestUrl.failActivity, {activityId: activityId, DIFFCITY: true}).then(res => {
            if(res.code === 0) {
              this.$message.success("设置失效成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [editItem 编辑]
       * @param  {[type]} _row   [description]
       * @param  {[type]} status [description]
       * @return {[type]}        [description]
       */
      editItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)
        this.$router.push(`/supplier_act/edit/${_row.id}/edit?status=${_row.activityStatus}`)
      },
      /**
       * [showItem 查看]
       * @param  {[type]} _row   [description]
       * @param  {[type]} status [description]
       * @return {[type]}        [description]
       */
      showItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)

        let activityId = _row.id
        this.$router.push("/supplier_act/show/" + activityId)
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }

  }
</script>


